export default class RegisterServices {
  static async register(data) {
    const response = await fetch(`https://www.eduagg.somee.com/users/register`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      },
       body: JSON.stringify(data)
    });
    return response.json();
  }

  static async dummy() {
    const response = await fetch(`https://u1206380.plsk.regruhosting.ru/courses`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
      }
    });
    return response.json();
  }
}
